@import '../index.scss';
.section_about {
  background-color: $color-grey-light-1;
  padding: 25rem 0;
  margin-top: -20vh;
}

.heading_secondary {
  font-size: 3.5rem;
  text-transform: uppercase;
  font-weight: 700;
  background-image: linear-gradient(
    to right,
    $color-primary-light,
    $color-primary-dark
  );
  -webkit-background-clip: text;
  color: transparent;
  letter-spacing: 0.2rem;
  transition: all 0.2s;

  &:hover {
    transform: skewY(2deg) skewX(15deg) scale(1.1);
  }
  text-align: center;
}

.row {
  max-width: 114rem;
  // background-color: #eee;
  margin: 0 auto;
  margin-top: 8rem;

  &:not(:last-child) {
    margin-bottom: 8rem;
  }
  @include clearfix;

  .col_1_of_2 {
    width: calc((100% - #{$gutter-horizontal}) / 2);
    // background-color: orangered;
    float: left;

    &:not(:last-child) {
      margin-right: $gutter-horizontal;
    }
  }
}

.heading_tertiary {
  font-size: $default-font-size;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 1.5rem;
}
.paragraph {
  font-size: $default-font-size;
  &:not(:last-child) {
    margin-bottom: 3rem;
  }
}
.button_text {
  &:link,
  &:visited {
    font-size: $default-font-size;
    color: $color-primary;
    display: inline;
    text-decoration: none;
    border-bottom: 1px solid $color-primary;
    padding: 0.3rem;
  }

  &:hover {
    background-color: $color-primary;
    color: $color-white;
    box-shadow: 0 1rem 2rem rgba($color-black, 0.15);
    transform: translateY(-2px);
    transition: all 0.4s;
  }
  &:active {
    box-shadow: 0 0.5rem 1rem rgba($color-black, 0.15);
    transform: translateY(0);
  }
}

.composition {
  position: relative;
  img {
    width: 55%;
    box-shadow: 0 1.5rem 4rem rgba($color-black, 0.4);
    // border-radius: 2px;
    position: absolute;
    z-index: 10;
    transition: all 0.2s;
    outline-offset: 2rem;

    &:hover {
      transform: scale(1.05) translateY(-0.5rem);
      box-shadow: 0 2.5rem 4rem rgba($color-black, 0.5);
      z-index: 20;
      outline: 1.5rem solid $color-primary;
    }
  }
  .photo_1 {
    left: 0;
    top: -2rem;
  }
  .photo_2 {
    right: 0;
    top: 2rem;
  }
  .photo_3 {
    left: 20%;
    top: 10rem;
  }
  &:hover img:not(:hover) {
    transform: scale(0.95);
  }
}
