@import '../index.scss';

.heading_secondary {
  font-size: 3.5rem;
  text-transform: uppercase;
  font-weight: 700;
  background-image: linear-gradient(
    to right,
    $color-primary-light,
    $color-primary-dark
  );
  -webkit-background-clip: text;
  color: transparent;
  letter-spacing: 0.2rem;
  transition: all 0.2s;

  &:hover {
    // transform: skewY(2deg) skewX(15deg) scale(1.1);
    transform: scale(1.1);
  }
  text-align: center;
}

.heading_tertiary {
  font-size: $default-font-size;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 1.5rem;
}
.paragraph {
  font-size: $default-font-size;
  &:not(:last-child) {
    margin-bottom: 3rem;
  }
}
