@import '../index.scss';
.row {
  max-width: 114rem;
  // background-color: #eee;
  margin: 0 auto;

  &:not(:last-child) {
    margin-bottom: 8rem;
  }
  @include clearfix;

  .col_1_of_3 {
    width: calc((100% - 2 * #{$gutter-horizontal}) / 3);
    // background-color: orangered;
    float: left;

    &:not(:last-child) {
      margin-right: $gutter-horizontal;
    }
  }
}

.section_tour {
  background-color: $color-grey-light-1;
  padding: 25rem 0 15rem 0;
  margin-top: -10rem;

  .heading_secondary {
    font-size: 3.5rem;
    text-transform: uppercase;
    font-weight: 700;
    background-image: linear-gradient(
      to right,
      $color-primary-light,
      $color-primary-dark
    );
    -webkit-background-clip: text;
    color: transparent;
    letter-spacing: 0.2rem;
    transition: all 0.2s;

    &:hover {
      transform: skewY(2deg) skewX(15deg) scale(1.1);
    }
    text-align: center;
  }
}

.button {
  text-align: center;
}
