@import '../index.scss';

.row {
  max-width: 114rem;
  background-color: #eee;
  margin: 0 auto;

  &:not(:last-child) {
    margin-bottom: 8rem;
  }
  @include clearfix;

  .col_1_of_2 {
    width: calc((100% - #{$gutter-horizontal}) / 2);
    background-color: orangered;
    float: left;

    &:not(:last-child) {
      margin-right: $gutter-horizontal;
    }

    @include clearfix;
  }

  .col_1_of_3 {
    width: calc((100% - 2 * #{$gutter-horizontal}) / 3);
    background-color: orangered;
    float: left;

    &:not(:last-child) {
      margin-right: $gutter-horizontal;
    }
  }

  .col_2_of_3 {
    width: calc(
      2 * (100% - 2 * #{$gutter-horizontal}) / 3 + #{$gutter-horizontal}
    );
    background-color: orangered;
    float: left;

    &:not(:last-child) {
      margin-right: $gutter-horizontal;
    }
  }
}
